import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "./Navbar.css";
import { RiMenu5Fill } from "react-icons/ri"; //

import { connect } from "react-redux";

const Navbar = ({ cart }) => {
  const [cartCount, setCartCount] = useState(0);

  const [showHamburger, setShowHamburger] = useState(false); //

  const handleClick = () => setShowHamburger(!showHamburger); //

  useEffect(() => {
    let count = 0;
    cart.forEach((cartItem) => {
      //loops thru cart array
      count += cartItem.qty;
    });
    setCartCount(count);
  }, [cart, cartCount]);

  return (
    <>
      <nav className="navbarContent">
        <div className="logoTitleNav">
          <img src="./egg.png" alt="logo" width="50vw" />
          <h1>The Naked Egg</h1>
        </div>
        <button onClick={() => setShowHamburger(!showHamburger)}>
          <RiMenu5Fill />
        </button>
        <div className="navLink" id={showHamburger ? "hidden" : ""}>
        <Link to="/" className="homeNav navLinks" onClick={handleClick}>
          Home
        </Link>
        <Link to="/ourstory" className="storyNav navLinks" onClick={handleClick}>
          Our Story
        </Link>
        <Link to="/productoverview" className="productsNav navLinks" onClick={handleClick}>
          Our Products
        </Link>
        <Link to="/cart" className="basketNav navLinks" onClick={handleClick}>
          Basket<i className="ri-shopping-basket-line"></i>&nbsp;{cartCount}
        </Link>
        <Link to="/login" className="loginNav navLinks" onClick={handleClick}>
          Login
        </Link>
        </div>
      </nav>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    cart: state.shop.cart,
  };
};

export default connect(mapStateToProps)(Navbar);
